#form-widgets-IMedialogDutchmanThemeSettings-extra_css { width: 100%; height: 100em; }

.plone-toolbar-left #edit-zone.pat-toolbar {z-index: 20}

#viewlet-below-content {
    @extend .container;
}
.template-edit .label {
  color: #000;
}

table.grid tbody tr td {
  padding: 4px 10px
}
 

.card {
position: relative;
display: -ms-flexbox;
display: flex;
-ms-flex-direction: column;
flex-direction: column;
min-width: 0;
word-wrap: break-word;
background-color: #fff;
background-clip: border-box;
border: 1px solid rgba(0, 0, 0, 0.125);
border-radius: 0.25rem;
}

.card-block  {
	padding: 1rem;
}



/* @group style */

#portal-globalnav-wrapper {
	z-index: 10
}

/* fix for mosaic padding and margin on fluid rows */
#content div.mosaic-grid-row.mosaic-grid-row-fluid {
	padding: 0;
	margin-left: -30px;
	margin-right: -30px;
}

.hidden_picture  {
 line-height: 1px;
}

.only-mobile,
div.only-mobile  {
	display: none !important;
}

.hidden {
	display: none !important;
}

.flex {
  display: flex;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-between {
	justify-content: space-between;
}

.flex-stretch {
	 align-items: stretch;
}

.full-width {
	width: 100%
}

.zoom  {
  transition: all 0.5s ease;
}

.zoom:hover {
  transform: scale(1.02);
}

.center-text {
	text-align: center;
}

.serif {
	font-family: $font-family-serif;
}

.sans-serif {
	font-family: $font-family-sans-serif;
}

.condensed {
	font-family:  $font-family-condensed;
}

.padding-0 {
