	margin: 0 -15px !important;
}

.large-text {
	font-size:24px;
}

.normal {
	font-weight: normal;
}

.bold {
	font-weight: bold;
}

.line-feed-1 {
	line-height: 120%;
}

.margin-10{
	margin:  10px !important;
}

.margin-10-0 {
	margin: 10px 0 !important;
}

img {
    height: auto;
}

img.img-responsive {
  margin-left:0;
  margin-right: 0;
  max-width: 100%;
  height: auto;
}

.flex-between {
	justify-content: space-between;
}

.flex-stretch {
	 align-items: stretch;
}

.half {
	box-sizing: border-box;
	width: 50%;
	min-width: 600px;
}

.half .half{
	min-width: 300px;
}

.half-height {
	height: 50%;
	min-width: 300px;
}

.full-width {
	width: 100%
}

/* @end */


@media screen and (max-width: 998px){


  .fullwidth {
	width: 100%;
	margin-left: 0;
	margin-right: 0;
	}

	div.flex {
  		flex-wrap: wrap;
	}


	.no-mobile {
		display:none;
	}

	 .only-mobile,
   div.only-mobile {
		display:block !important;
	}
}

@media screen and (max-width: 768px){

    div.flex {
	     flex-direction: column;
       flex-wrap: wrap;
    }

    .no-mobile {
		    display:none;
    }
}

 
